.offerings {
    padding-top: 260px;
    padding-bottom: 131px;
    background-image: url(../images/Offerings/bg-mob.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: -160px;

    @media screen and (min-width: 1200px) {
        background-image: url(../images/Offerings/bg-tab.png);
    }

    @media screen and (min-width: 1200px) {
        padding-top: 260px;
        padding-bottom: 131px;
        margin-top: -160px;
        background-image: url(../images/Offerings/bg@1x-des.jpg);

        @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/Offerings/bg@2x-des.jpg);
        }
    }
}

.offerings__container {

    @media screen and (min-width: 1200px) {
        display: flex;
        align-items: center;
        gap: 90px;
    }
}

.img__border {
    width: 550px;
    height: 550px;
    border-radius: 50%;
    border: 5px solid rgba(255, 255, 255, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.img {
    margin-bottom: 80px;
    width: 396px;
}

.img__background {
    border-radius: 50%;
    border: none;
    width: 502px;
    height: 502px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: radial-gradient(50% 50% at 50% 50%,
            #2f583f 0%,
            rgba(105, 105, 105, 0) 100%);
    background-position: bottom center;
    background-size: 252px 87px;
    background-repeat: no-repeat;
}

.offerings__title--wrap {
    text-align: start;
    width: 573px;
    margin-right: 35px;
}

.offerings__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 1.2;
    margin-bottom: 20px;
    color: $PraimaryTextColor;
}

.offerings__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 40px;
    color: $PraimaryTextColor;
}

.offerings__list {
    display: flex;
    gap: 67px;
    margin: 0;
    padding: 0;
    margin-bottom: 32px;
    list-style: none;
}

.offerings__item {
    display: flex;
    flex-direction: column;
}

.offerings__item__icon--bg {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #ffffff;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.offerings__item__title {
    font-weight: 700;
    font-size: 17.0833px;
    line-height: 1.4;
    margin-bottom: 16px;
    color: $PraimaryTextColor;
}

.offerings__item__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.5;
    color: $PraimaryTextColor;
}

.offerings__btn {
    padding: 10px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.5;
    color: $PraimaryTextColor;
    background-color: #ffffff;
    border: 2px solid $MainBorderColor;
    border-radius: 30px;
    cursor: pointer;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus,
    &:active {
        border: 2px solid $AccentBorderColor;
    }
}